<template>
<el-container>
<el-header height="70px"><el-image :src="src" style="height:100%"></el-image></el-header>
  <el-container>
  <el-aside width="150px">
    <el-menu router
      default-active="1"
      class="el-menu-vertical-demo"
      background-color="#333"
      text-color="#fff">
      <el-menu-item index="/mainPage">
         <i class="el-icon-menu"></i>
        <span slot="title">首页</span>
      </el-menu-item>
      <el-menu-item index="/manage">
        <i class="el-icon-s-order"></i>
        <span slot="title">管理</span>
      </el-menu-item>
      <el-menu-item index="/chart">
        <i class="el-icon-s-order"></i>
        <span slot="title">图表</span>
      </el-menu-item>
      <el-menu-item index="/config">
        <i class="el-icon-setting"></i>
        <span slot="title">设置</span>
      </el-menu-item>
    </el-menu>
</el-aside>
    <el-main>
      <router-view/>
    </el-main>
  </el-container>
</el-container>
</template>

<script>
import src from '@/assets/element.svg'
export default {
    data() {
      return {
        src: src
      }
    }
}
</script>

<style>
  .el-header, .el-footer {
    background-color: #333;
  }
  
  .el-aside {
    background-color: #D3DCE6;
    color: #333;
  }
  
  .el-main {
    background-color: #E9EEF3;
    color: #333;
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }
  p{
    line-height: 50px;
    color: #FFF;
  }
  .el-aside{
      background-color: #333;
  }
  .el-menu{
    border: #333;
  }
  html,body,#app,.el-container{
      padding: 0px;
      margin: 0px;
      height: 100%;
  }
</style>



